.wrapper {
  overflow: hidden;
}
.btn-text {
  position: relative;
  z-index: 2;
}
.btn-orange {
  position: relative;
  background: #DD7500;
  border-radius: 100px;
  color: #FFF1DA;
  .btn-text {
    line-height: 100%;
    text-transform: uppercase;
  }
  .circle {
    background: #BD6400;
  }
}
.btn-brown {
  position: relative;
  background: rgba(221, 117, 0, 0.2);
  border-radius: 100px;
  color: #1B1D25;
  &:hover {
    color: #FFF1DA;
  }
  .btn-text {
    line-height: 100%;
    text-transform: uppercase;
  }
  .circle {
    background: #DD7500;
  }
}
.btn-animation {
  position: relative;
  overflow: hidden;
  &:hover {
    .circle {
      width: 200%;
      height: 400%;
    }
  }
  .circle {
    position: absolute;
    z-index: 0;
    display: block;
    width: 0;
    height: 0;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    transition: width 0.7s, height 0.7s;
  }
}
.form {
  &-group {
    position: relative;
    input {
      width: 100%;
      background: transparent;
      border: 1px solid rgba(27, 29, 37, 0.3);
      border-radius: 100px;
      font-size: 12px;
      line-height: 100%;
      letter-spacing: 1.2px;
      text-transform: uppercase;
      color: #1B1D25;
    }
    .input-info {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translate(0, -50%);
      opacity: 0;
      pointer-events: none;
      transition: all 0.125s;
      &:hover {
        .input-info__text {
          opacity: 1;
          pointer-events: auto;
        }
      }
      &__icon {
        width: 16px;
        cursor: pointer;
        img {
          width: 100%;
        }
      }
      &__text {
        position: absolute;
        left: 0;
        bottom: 100%;
        width: max-content;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #fff;
        border-radius: 15px;
        padding: 4px 12px;
        font-size: 12px;
        line-height: 120%;
        opacity: 0;
        pointer-events: none;
        transition: all 0.125s;
      }
    }
  }
  &-group--active {
    input {
      border-color: #CA3011;
      color: #CA3011;
      &::placeholder {
        color: #CA3011;
      }
    }
    .input-info {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label:before {
      background-image: url("../img/check.svg");
    }
    label {
      position: relative;
      display: block;
      padding-left: 24px;
      font-size: 10px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: 0.6px;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 14px;
        height: 14px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 1px solid #1B1D25;
        border-radius: 4px;
      }
      a {
        position: relative;
        z-index: 3;
      }
    }
  }
}


