.header {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  padding: 35px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__logo {
    width: 112px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 40px;
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 40px;
    li a {
      font-size: 11px;
      line-height: 100%;
      letter-spacing: 1.1px;
      text-transform: uppercase;
      &:hover {
        color: #DD7500;
      }
    }
  }
  &__btn {
    height: 24px;
    padding: 0 17px;
    .btn-text {
      font-size: 11px;
      letter-spacing: 1.1px;
    }
  }
  &__group2 {
    display: flex;
    align-items: center;
    gap: 50px;
    .header__btn {
      display: none;
    }
  }
  &__phone {
    font-size: 11px;
    line-height: 100%;
    letter-spacing: 1.1px;
    &:hover {
      color: #DD7500;
    }
  }
}
.burger {
  position: relative;
  width: 40px;
  height: 6px;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background: #FFF1DA;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    bottom: 0;
  }
}
.menu {
  position: fixed;
  z-index: 12;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #FFF1DA;
  border-radius: 0 0 36px 36px;
  padding: 40px 60px 60px 60px;
  color: #1B1D25;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  overflow-y: auto;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
  .container {
    padding: 0;
  }
  &__inner {
    position: relative;
  }
  &__close {
    position: absolute;
    top: -5px;
    right: 0;
    width: 30px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__wrap {
    max-width: 1100px;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 60px;
  }
  &__left {
    max-width: 400px;
  }
  &__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 40px;
  }
  &__logo {
    max-width: 400px;
    display: block;
    margin-bottom: 85px;
    img {
      width: 100%;
    }
  }
  &__logo-mob {
    display: none;
  }
  .form {
    max-width: 335px;
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    margin-bottom: 60px;
    &-group {
      input {
        height: 54px;
        padding: 0 30px;
      }
    }
    &-btn {
      width: 100%;
      height: 54px;
      padding: 5px 15px;

      .btn-text {
        font-size: 12px;
      }
    }
    &-check {
      max-width: 306px;
    }
  }
  &__video {
    position: relative;
    text-align: start;
    background: transparent;
    padding: 0;
    &-text {
      position: absolute;
      z-index: 2;
      top: 10px;
      right: 10px;
      display: flex;
      align-items: center;
      gap: 6px;
      span {
        font-size: 16px;
        line-height: 130%;
        letter-spacing: 1.6px;
        text-transform: uppercase;
        color: #DD7500;
      }
      i {
        width: 40px;
        height: 40px;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #DD7500;
        border-radius: 50%;
        padding: 10px;
        img {
          max-width: 100%;
        }
      }
    }
    &-img {
      display: block;
      img {
        width: 100%;
        border-radius: 24px;
      }
      img:nth-child(2) {
        display: none;
      }
    }
  }
  &__video-mob {
    display: none;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-bottom: 40px;
    li a {
      font-size: 16px;
      line-height: 100%;
      letter-spacing: 1.6px;
      text-transform: uppercase;
      &:hover {
        color: #DD7500;
      }
    }
  }
  &__list2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
    li a {
      font-size: 11px;
      line-height: 100%;
      letter-spacing: 1.1px;
      text-transform: uppercase;
      &:hover {
        color: #DD7500;
      }
    }
  }
  &__btn {
    height: 54px;
    padding: 5px 42px;
    margin-bottom: 40px;
    span {
      font-size: 12px;
    }
  }
  &__phone {
    display: block;
    font-family: 'Vetrino';
    font-size: 30px;
    line-height: 100%;
    letter-spacing: -1.2px;
    margin-bottom: 10px;
    &:hover {
      color: #DD7500;
    }
    &-wrap {
      margin-bottom: 30px;
    }
    &-btn {
      background: transparent;
      padding: 0;
      font-size: 14px;
      line-height: 120%;
      letter-spacing: 0.84px;
      &:hover {
        color: #DD7500;
      }
    }
    &-text {
      display: none;
      font-size: 14px;
      line-height: 120%;
      letter-spacing: -0.28px;
      margin-bottom: 10px;
    }
  }
  &__location,
  &__time {
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.84px;
  }
  &__location {
    margin-bottom: 10px;
  }
  &__time {
    margin-bottom: 30px;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &__social {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #DD7500;
    border-radius: 50%;
    padding: 10px;
    &:hover {
      background: #BD6400;
    }
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 20px 0;
    &__inner {
      gap: 30px;
    }
    &__group {
      display: none;
    }
    &__group2 {
      gap: 30px;
      .header__btn {
        display: flex;
      }
    }
    &__phone {
      display: none;
    }
  }
  .menu {
    border-radius: 0;
    padding: 20px;
    &__wrap {
      flex-direction: column;
      align-items: start;
      gap: 20px;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      order: -1;
      gap: 20px;
    }
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 15px;
      margin-bottom: 30px;
    }
    &__close {
      position: static;
      width: 18px;
    }
    &__logo {
      max-width: 140px;
      display: none;
      margin: 0;
    }
    &__logo-mob {
      display: block;
    }
    .form {
      display: none;
    }
    &__list {
      gap: 12px;
      margin-bottom: 20px;
      li a {
        font-size: 13px;
        letter-spacing: 1.3px;
      }
    }
    &__btn {
      width: 100%;
      height: 44px;
      margin-bottom: 20px;
    }
    &__video {
      display: none;
      &-img {
        img:nth-child(1) {
          display: none;
        }
        img:nth-child(2) {
          display: block;
        }
      }
      &-text {
        span {
          font-size: 13px;
          letter-spacing: 1.3px;
        }
      }
    }
    &__video-mob {
      display: block;
    }
    &__phone {
      font-size: 24px;
      letter-spacing: -0.24px;
      &-wrap {
        margin-bottom: 24px;
      }
      &-text {
        display: block;
      }
      &-btn {
        font-size: 12px;
        letter-spacing: 0.72px;
      }
    }
    &__location,
    &__time {
      font-size: 12px;
      letter-spacing: 0.72px;
    }
    &__time {
      margin-bottom: 24px;
    }
  }
}

@media screen and (max-width: 359px) {
  .header {
    &__inner {
      gap: 20px;
    }
    &__group2 {
      gap: 20px;
    }
  }
}