.main {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 210px 0 60px;
  color: #FFF1DA;
  transition: background-image 1.5s ease-in-out;
  &__title {
    max-width: 1250px;
    text-align: center;
    font-family: 'Vetrino';
    font-size: 80px;
    line-height: 100%;
    font-weight: 400;
    letter-spacing: 0.8px;
    margin-bottom: 40px;
    span {
      color: #DD7500;
    }
  }
  &__btn {
    height: 54px;
    text-align: start;
    gap: 10px;
    padding: 5px 12px 5px 78px;
    margin: 0 auto 100px auto;
    .btn-icon {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      max-width: 65px;
      img {
        max-width: 100%;
      }
    }
    .btn-text {
      font-size: 10px;
    }
    .btn-play {
      position: relative;
      z-index: 2;
      width: 30px;
      height: 30px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFF1DA;
      border-radius: 50%;
      padding: 10px;
      img {
        max-width: 100%;
      }
    }
  }
  &__items {
    max-width: 830px;
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 90px / 3));
    gap: 45px;
    padding: 0 30px;
    margin: 0 auto;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &-number {
      font-family: 'Vetrino';
      font-size: 20px;
      line-height: 100%;
      letter-spacing: -0.8px;
      margin-bottom: 15px;
      span {
        font-size: 56px;
        letter-spacing: 2.24px;
      }
    }
    &-text {
      font-size: 10px;
      line-height: 140%;
      letter-spacing: 1px;
      text-transform: uppercase;
    }
  }
  &__mob {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .main {
    &__title {
      font-size: 60px;
      letter-spacing: 0.6px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    background: transparent;
    padding: 0;
    .container {
      display: none;
    }
    &__title {
      font-size: 38px;
      letter-spacing: 0.38px;
      margin-bottom: 50px;
    }
    &__btn {
      height: 44px;
      padding: 5px 10px 5px 65px;
      margin-bottom: 0;
      .btn-icon {
        max-width: 55px;
      }
      .btn-text {
        font-size: 8px;
      }
      .btn-play {
        width: 25px;
        height: 25px;
      }
    }
    &__item {
      &-number {
        font-size: 24px;
        letter-spacing: -0.24px;
        margin-bottom: 30px;
        span {
          font-size: 38px;
          letter-spacing: 0.38px;
        }
      }
      &-text {
        font-size: 11px;
      }
    }
    &__mob {
      position: relative;
      min-height: 700px;
      display: block;
    }
    &__mob-group {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      padding: 164px 20px;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.5s ease;
      visibility: hidden;
    }
    &__mob-group.active {
      opacity: 1;
      visibility: visible;
      pointer-events: auto;
    }
  }
}